.footer-container{
    height: 17.8%;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;

}

.footer-parent{
    width: 100%;
    height: 100%;
    max-width: 1920px;
}

.footer-parent img{
    max-width: 100%;
    height: 130px;
}

@media screen and (max-width:1110px) {
    .footer-parent{
        display: flex;
        align-items: center;

    }
    
} 


@media screen and (max-width:400px) {
    .footer-parent img{
        width: 100%;
        height: 25px;
    }
    
}
@media screen and (max-width:900px) {
    .footer-parent img{
        width: 100%;
        height: 25px;
    }
    
}